<template>
    <div class="container">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>首页</el-breadcrumb-item>
        <el-breadcrumb-item>供应商列表</el-breadcrumb-item>
      </el-breadcrumb>
      <div class="tools">
          <el-button type="primary" class="addItem" @click="addsupplier()">新增供应商</el-button>
      </div>
      <div class="tableContainer">
        <el-table
          :data="tableData"
          height="100%"
          border
          style="width: 100%" class="table">
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-form label-position="left" inline class="demo-table-expand">
                <el-form-item label="供应商id">
                  <span>{{ props.row.id }}</span>
                </el-form-item>
                <el-form-item label="供应商名">
                  <span>{{ props.row.name }}</span>
                </el-form-item>
                <el-form-item label="地区">
                  <span>{{ props.row.area }}</span>
                </el-form-item>
                <el-form-item label="平台">
                  <span>{{ props.row.platform }}</span>
                </el-form-item>
                <el-form-item label="采购价">
                  <span>{{ props.row.purchase_price}}</span>
                </el-form-item>
                <el-form-item label="最少量">
                  <span>{{ props.row.minimum_quantity }}</span>
                </el-form-item>
                <el-form-item label="联系人">
                  <span>{{ props.row.link_name }}</span>
                </el-form-item>
                <el-form-item label="电话 ">
                  <span>{{ props.row.link_phone }}</span>
                </el-form-item>
                <el-form-item label="邮箱">
                  <span>{{ props.row.email }}</span>
                </el-form-item>
                <el-form-item label="创建时间">
                  <span style="color: #00a0e9">{{ props.row.create_time }}</span>
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <el-table-column
            prop="id"
            label="供应商id">
          </el-table-column>
          <el-table-column
            prop="name"
            label="供应商名">
          </el-table-column>
          <el-table-column
            prop="area"
            label="地区">
          </el-table-column>
          <el-table-column
            prop="platform"
            label="平台">
          </el-table-column>
          <el-table-column
            prop="purchase_price"
            label="采购价">
          </el-table-column>
          <el-table-column
            prop="minimum_quantity"
            label="最少量">
          </el-table-column>
          <el-table-column
            prop="link_name"
            label="联系人">
          </el-table-column>
          <el-table-column
            prop="link_phone"
            label="电话">
          </el-table-column>
          <el-table-column
            prop="email"
            label="邮箱">
          </el-table-column>
          <el-table-column
            prop="create_time"
            label="创建时间">
          </el-table-column>
          <el-table-column
            label="操作">
            <template slot-scope="scope">
              <el-button @click="handleDelete(scope.row,scope.$index)" type="text" size="small">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[100, 200, 300, 400]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalSize">
        </el-pagination>
      </div>
    </div>
</template>

<script>
  const language = {
    'CN': '简体中文',
    'HK': '繁体中文-香港',
    'TW': '繁体中文-台湾',
    'EN': '英语',
    'ID': '印尼语',
    'TH': '泰语',
    'MY': '马来语',
    'UA': '阿拉伯语'
  }
  const degree = ["低","中","高"];
  const statusArr = ['新增','翻译通过','翻译待审核','翻译不通过','翻译进行中'];
  export default {
    name: "submitDemand",
    data() {
        return {
          tableData: [],
          totalSize: 0,
          currentPage: 1,
          pageSize: 100,
          statusValue: '-1',
          degreeValue: '-1',
          statusOptions: [
            {
              value: '-1',
              label: '所有状态'
            },
            {
              value: '0',
              label: '新增'
            }, {
              value: '1',
              label: '翻译通过'
            }, {
              value: '2',
              label: '翻译待审核'
            }, {
              value: '3',
              label: '翻译不通过'
            }, {
              value: '4',
              label: '翻译进行中'
            }
          ],
          degreeOptions: [
            {
              value: '-1',
              label: '所有等级'
            },
            {
              value: '0',
              label: '低'
            }, {
              value: '1',
              label: '中'
            }, {
              value: '2',
              label: '高'
            }
          ]
        }
    },
    methods: {
      degreeChange(val) {
        if(val >= 0){
          if(this.statusValue >= 0){
            this.initTable(val,this.statusValue);
          }else {
            this.initTable(val,null);
          }
        }else {
          if(this.statusValue >= 0){
            this.initTable(null,this.statusValue);
          }else {
            this.initTable();
          }
        }
      },
      statusChange(val) {
        if(val >= 0){
          if(this.degreeValue >= 0){
            this.initTable(this.degreeValue,val);
          }else{
            this.initTable(null,val);
          }
        }else {
          if(this.degreeValue >= 0){
            this.initTable(this.degreeValue,null);
          }else{
            this.initTable();
          }
        }
      },
      filterDegree(value, row, column) {
        const property = column['property'];
        return row[property] === this.degreeOptions[parseInt(value)+1].label;
      },
      filterStatus(value, row, column) {
        const property = column['property'];
        return row[property] === this.statusOptions[parseInt(value)+1].label;
      },
      handleSizeChange(val) {
        this.pageSize = val;
        this.initTable();
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.initTable();
      },
      addsupplier() {
        this.$router.push('/product/supplier/add')
      },
      handleEdit(row) {
        this.$router.push({ path: '/supplierManage/updatesupplier',query: row})
      },
      handleDelete(row,index) {
        let url = '/v1/suppliers/'+row.id;
        let that = this;
        this.$confirm('是否确认删除该条记录?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          that.$axios.delete(url).then((res) => {
            if(res.data.code == 200){
              that.tableData.splice(index,1);
              that.$message({
                type: 'success',
                message: '删除成功!'
              });
            }else {
              this.$message({
                type: 'error',
                message: '删除失败!'
              });
            }
          }).catch(() => {
            this.$message({
              type: 'error',
              message: '删除失败!'
            });
          })
        }).catch(() => {});
      },
      initTable(level,status) {
        let initList = [];
        let url = "/v1/suppliers?page="+this.currentPage+"&pagesize="+this.pageSize;
        if(level){
          url += "&level="+level;
        }
        if(status){
          url += "&status="+status;
        }
        let that = this;
        this.$axios.get(url).then((res) => {
          if(res.data.code == 200){

            let suppliersList = res.data.data.list;
            console.log(suppliersList);

            that.totalSize = parseInt(res.data.data.page_total);
            suppliersList.forEach((supplier) => {
              let tempsupplier = {};
              tempsupplier.id = supplier.id;
              tempsupplier.name = supplier.name;
              tempsupplier.area = supplier.area;
              tempsupplier.platform = supplier.platform;
              tempsupplier.purchase_price = supplier.purchase_price;
              tempsupplier.minimum_quantity = supplier.minimum_quantity;
              tempsupplier.link_name = supplier.link_name;
              tempsupplier.link_phone= supplier.link_phone;
              tempsupplier.email = supplier.email;
              tempsupplier.create_time = supplier.create_time;
              initList.push(tempsupplier);
            })
          }
          that.tableData = initList;
        }).catch((err) => {})
      }
    },
    created() {
      this.initTable();
    }
  }
</script>

<style scoped lang="stylus">
  .container
    padding 30px
    position relative
    .tools
      margin 20px 0
      .selected
        width: 150px
        margin-right: 20px
      .addItem
        float: right
    .tableContainer
      position absolute
      left 30px
      right: 30px
      top: 120px
      bottom: 0
      display flex
      flex-direction column
      .table
        flex 1
        .demo-table-expand
          font-size: 0
          label
            width: 90px
            color: #99a9bf
          .el-form-item
            margin-right: 0
            margin-bottom: 0
            width: 33.333%
        td.markColor
          color #00a0e9
        a
          color #03B8CC
      .el-pagination
        text-align right
        margin 8px 0
</style>



